  :[121.585621, 24.990869],
  :[110.67598, 31.74452]
};

var color= ["#a0e85b", "#1b511d", "#c6edc5", "#0f144c", "#967ff2", "#803673", "#bf11af", "#edcbde", "#1a119b", "#918ea2", "#445082", "#fa79f5", "#8c2efc", "#c479a3", "#faf679", "#829951", "#30972e", "#3a2006", "#e9ad6f", "#84541a", "#ed4b04", "#8f0f1b"];
var AirlinesList= [];
var AerodromesList= [];

// 
var planePath =
  "path://M1705.06,1318.313v-89.254l-319.9-221.799l0.073-208.063c0.521-84.662-26.629-121.796-63.961-121.491c-37.332-0.305-64.482,36.829-63.961,121.491l0.073,208.063l-319.9,221.799v89.254l330.343-157.288l12.238,241.308l-134.449,92.931l0.531,42.034l175.125-42.917l175.125,42.917l0.531-42.034l-134.449-92.931l12.238-241.308L1705.06,1318.313z";
var convertData = function (data) {
  var res = []; 
  for (var i = 0; i < data.length; i++) {
    var dataItem = data[i];
    var fromCoord = geoCoordMap[dataItem.from];  //from
    var toCoord = geoCoordMap[dataItem.to];    //to
    if (fromCoord && toCoord) { //
      res.push({
        fromName: dataItem.from,
        toName: dataItem.to,
        id: i+1,
        coords: [fromCoord, toCoord], //
        effect:{
          period: dataItem.value * 3.5  //value 
        },
        lineStyle: {
          normal: {
            color: color[i]  //
          }      
        }
      });
    }
  }
  return res;
};
var series = [];
  series.push(
    { //arrow
      //name: item[0] + " Top3",
      type: "lines",
      zlevel: 1,
      effect: {
        show: true,
        period: 10,    // s
        trailLength: 0, // [0,1]
        color: "red",   //arrow
        symbolSize: 0
      },
      lineStyle: {
        normal: {
          color: "#fff",  //
          width: 0,
          curveness: 0  // 
        }
      },
      data: convertData(AirlinesList)
    },
    {
      //name: item[0] + " Top3",
      type: "lines",
      // stateAnimationduration: 0,
      animation: false,
      zlevel: 2,
      symbol: ["none", "none"],
      symbolSize: 0,
      effect: {
        show: true,
        period: 10,
        trailLength: 0,
        symbol: planePath,
        symbolSize: 0
      },
      lineStyle: {
        normal: {
          color: "#FFF",  //
          width: 0,
          opacity: 0.6,
          curveness: 0.4
        }
      },
      data: convertData(AirlinesList)
    },
    {
      //name: item[0] + " Top3",
      type: "effectScatter",
      coordinateSystem: "geo",
      zlevel: 2,
      data:  AerodromesList.map(function (dataItem) {
        return {
          name: dataItem.name,
          value: geoCoordMap[dataItem.name].concat([dataItem.value]),
        };
      }),
      rippleEffect: {
        brushType: "stroke"
      },
      label: {
        normal: {
          show: true,
          position: "right",
          formatter: "{b}"
        }
      },
      symbolSize: 10,  //  
      itemStyle: {
        normal: {
          // color: "#18212b",  //
          color: function (params){ //value
            if( params.value[2] != 100){
              return '#c23531'; //
            }else {
              return '#f878f3'; //
            }
          }
        },
        emphasis: {
          areaColor: "#2B91B7"
        }
      },
    }
  );

export default {

  data: function () {
    return {
      myChart: {},
      AerodromesList: [],
      year: 2019,
      options: {
        tooltip: {
          trigger: "item",
          formatter: function (params, ticket, callback) {  //
            if (params.seriesType == "effectScatter") {
              if(params.data.value[2] != 100)
                return params.data.name + "";
              else
                return params.data.name + ""; //+ params.data.value[2];
            } else if (params.seriesType == "lines") {
              return (
                params.data.id + ": " + params.data.fromName +  "->" + params.data.toName 
              );
            } else {
              return params.name;
            }
          }
        },
        geo: {
          map: "china",
          label: {
            emphasis: {
              show: true,
              color: "#fff"
            }
          },
          roam: false,
          //   
          zoom: 1,
          itemStyle: {
            normal: {
              areaColor: "rgba(43, 196, 243, 0.42)",
              borderColor: "rgba(43, 196, 243, 1)",
              borderWidth: 1
            },
            emphasis: {
              areaColor: "#2B91B7"
            }
          }
        },
        series: series
      },
      size: {
        width: '100%',
        height: '12rem'
      }
    }
  },
  watch: {
    // options
    options: function () {
      //this.myChart.clear();
      this.myChart.setOption(this.options);
    }
  },
  mounted () {
    // div
    // var mainDiv = document.getElementById('main')
    // domecharts
    this.myChart = echarts.init(this.$refs.echarts);
    // 
    this.myChart.setOption(this.options);
    let that = this;
    this.myChart.on('click',function(params){
      for (var i = 0; i < that.AerodromesList.length; i++) {
        if(that.AerodromesList[i].name == params.name)
          if(that.AerodromesList[i].value == 0)
            that.AerodromesList[i].value = 100
          else
            that.AerodromesList[i].value = 0
          console.log(that.AerodromesList[i].name+":"+that.AerodromesList[i].value)
      }
      that.options = { ...that.options }
    })
  },
  created() {
    // 
    this.AerodromesList = require('@/static/json/Aerodromes_A319');
    const AirlinesJson = require('@/static/json/Airlines_A319');
    // const AirlinesJson = require('@/static/json/test1');
    var currentTime = 0;
    setInterval(() => {
      this.AirlinesList = AirlinesJson[currentTime]
      this.options.series[0].data = convertData(this.AirlinesList)
      this.options.series[1].data = convertData(this.AirlinesList)
      this.AerodromesList.map(function (dataItem) {
        if(geoCoordMap[dataItem.name] == undefined)
          alert(dataItem.name + "")
      })
      this.options.series[2].data = this.AerodromesList.map(function (dataItem) {
        return {
          name: dataItem.name,
          value: geoCoordMap[dataItem.name].concat([dataItem.value])
        };
      })
      currentTime ++;
      if(currentTime >= AirlinesJson.length)
        currentTime = 0;
      this.options = { ...this.options }
    }, 3000)  //
  }
}
</script>

